const DOMNodeCollection = require('./dom_node_collection.js');

window.$l = selector => {
  const nodes = (selector instanceof HTMLElement
                    ? [selector]
                    : Array.from(document.querySelectorAll(selector)));
  return DOMNodeCollection(nodes);
};



