$l.ajax = (options) => {
  const defaults = {
    method: 'GET',
    url: 'https://www.reddit.com/r/The_Donald',
    async: true,
    contentType: 'application/x-www-form-urlencoded; charset=UTF-8',
    dataType: 'json',
    data: {
      body: {
        president: "Donald Trump",
        slogan: "MAGA"
      }
    },
    success: console.log("Yay, you reached the donald!"),
    error: console.log("Boohoo, you failed to reach the donald")
  };
  const queryOptions = $l.extend(defaults, options);
  const xhr = new XMLHttpRequest();
  xhr.open(queryOptions.method, queryOptions.url);
  xhr.onload = () => {
    console.log(xhr.status);
    console.log(xhr.responseType);
    console.log(xhr.response);
  }
  xhr.send(JSON.parse(queryOptions));
}



